const request = require('supertest');

describe('POST /add-task', function () {
    it('Adds a task', function (done) {
        // Use supertest to run assertions for our API
        request("http://localhost:4000/app")
            .get('/user/getUser/0')
            .set("Authorization", "Bearer " + "123321123")
            .expect(200, done);
    });
});
